import { Accordion, Button, Card, Form, InputGroup, OverlayTrigger, Table, Tooltip } from "react-bootstrap";
import { Saving, SavingsAtDate, useFinance } from "./FinanceContext";
import { ChangeEvent, useEffect, useMemo } from "react";
import { DatePicker, LocalizationProvider } from "@mui/x-date-pickers";
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import dayjs, { Dayjs } from 'dayjs'
import { formatDate, formatNumber } from "./FormattingHelpers";
import { SavingsAtDateCalculator } from "./SavingsCalculators";

const InputSavingsData: React.FC = () =>
{
    const {config, setConfig, savingsAtDates, setSavingsAtDates, addSavingsAtDate, updateSavingsAtDate, deleteSavingsAtDate, duplicateSavingsAtDate, updateSavings, deleteSaving, addSaving} = useFinance();
    
    const sortedSavingsAtDates = useMemo(() => {

        return [...savingsAtDates].sort((a, b) => {
            const date1 = config.sortSavingsAscendingOrDescending === "ascending" ? new Date(a.date) : new Date(b.date);
            const date2 = config.sortSavingsAscendingOrDescending === "ascending" ? new Date(b.date) : new Date(a.date);
            return date1.getTime() - date2.getTime();
        });
    }, [savingsAtDates, config.sortSavingsAscendingOrDescending]);

    useEffect(() => {
        if (JSON.stringify(sortedSavingsAtDates) !== JSON.stringify(savingsAtDates)) {
            setSavingsAtDates(sortedSavingsAtDates);
        }
    }, [sortedSavingsAtDates]);

    const renderSaving = (saving: Saving, index: number, parentIndex: number) =>
    {
        return(
            <tr>
                <td>
                    <Form.Control
                        type='text'
                        onChange={(e: { target: { value: string; }; }) => updateSavings(parentIndex, index, { ...saving, name: e.target.value })}
                        value={saving.name}
                        className="mr-2" />
                </td>
                <td>
                    <Form.Control
                        type='number'
                        onChange={(e: ChangeEvent<HTMLInputElement>) => 
                            updateSavings(parentIndex, index, { ...saving, thisMonthsContribution: parseFloat(e.target.value) })
                        }
                        value={saving.thisMonthsContribution}
                        className="mr-2" />
                </td>
                <td>
                    <Form.Control
                        type='number'
                        onChange={(e: ChangeEvent<HTMLInputElement>) => 
                            updateSavings(parentIndex, index, { ...saving, currentValue: parseFloat(e.target.value) })
                        }
                        value={saving.currentValue}
                        className="mr-2" />
                </td>
                <td>
                    <Form.Select
                        onChange={(e: ChangeEvent<HTMLSelectElement>) => 
                            updateSavings(parentIndex, index, { ...saving, accessibility: e.target.value })
                        }
                        value={saving.accessibility}
                    >
                        <option value = "sameDay"> Immediate </option>
                        <option value = "upToThreeDays"> Up to Three Days </option>
                        <option value = "moreThanThreeDays"> More Than Three Days </option>
                    </Form.Select>
                </td>
                <td>
                    <InputGroup className="mb-3 mt-2">
                        <Form.Check
                            type="switch"
                            onChange={(e: ChangeEvent<HTMLInputElement>) => 
                                updateSavings(parentIndex, index, { ...saving, taxableIncome:(e.target.checked) })
                            }
                            checked={saving.taxableIncome}
                            className="mt-3;"
                            />
                    </InputGroup>
                </td>
                <td>
                    <OverlayTrigger placement="top"
                                    overlay={
                                        <Tooltip>
                                            How risky is this investment? 1 being not at all, 100 being extremely
                                        </Tooltip>
                                    }
                    >
                        <InputGroup className="mb-3 mt-2">
                            <Form.Control
                                type='number'
                                min="1"
                                max="100"
                                onChange={(e: ChangeEvent<HTMLInputElement>) => 
                                    updateSavings(parentIndex, index, { ...saving, riskScore: parseFloat(e.target.value) })
                                }
                                value={saving.riskScore}
                                className="mr-2" />
                        </InputGroup>
                    </OverlayTrigger>
                </td>
                <td>
                    <Button variant="danger" onClick={() => deleteSaving(parentIndex, index)}>X</Button>
                </td>
            </tr>
        )
    }

    const SavingsForDate = (savingsAtDate: SavingsAtDate, dateIndex: number) =>
    {
        return(
        <>
            <Table striped bordered hover>
                <thead>
                    <tr>
                        <th>
                            Name
                        </th>
                        <th>
                            Change In Investment
                        </th>
                        <th>
                            Current Value
                        </th>
                        <th>
                            Accessibility
                        </th>
                        <th>
                            Taxable Income
                        </th>
                        <th>
                            Risk Score
                        </th>
                    </tr>
                </thead>
                <tbody>
                    {savingsAtDate.investments.map((saving, index: number) => renderSaving(saving, index, dateIndex))}
                </tbody>
            </Table>
            <Button variant="primary" onClick={() => addSaving(dateIndex)}>Add Saving</Button>
        </>
        )
    }

    const getWeightedRisk = (dateIndex: number) =>
    {
        const savingsAtDateCalculator = new SavingsAtDateCalculator(savingsAtDates[dateIndex], savingsAtDates)
        return (<>Weighted Risk: {formatNumber(savingsAtDateCalculator.weightedRisk)}</>)
    }

    const SavingsAtDate = (savingsAtDate: SavingsAtDate, dateIndex: number) =>
    {
        return(
        <>
            <Accordion.Item eventKey={String(dateIndex)}>
                <Accordion.Header>
                    <b>{formatDate(savingsAtDate.date)} {getWeightedRisk(dateIndex)}</b>
                </Accordion.Header>
                <Accordion.Body>
                    <LocalizationProvider dateAdapter={AdapterDayjs}>
                        <DatePicker
                            label="Date of Investment Snapshot"
                            onChange={(newValue: Dayjs | null) => {
                                    const date = newValue ? newValue.toDate() : new Date();
                                    updateSavingsAtDate(dateIndex, {...savingsAtDate, date: date})}
                                }
                            value={dayjs(savingsAtDate.date)}
                            />
                    </LocalizationProvider>
                    {SavingsForDate(savingsAtDate, dateIndex)}
                <Card.Footer>
                    <Button variant="primary" onClick={() => duplicateSavingsAtDate(dateIndex)}>Duplicate</Button>
                    <Button variant="danger" onClick={() => deleteSavingsAtDate(dateIndex)}>X</Button>
                </Card.Footer>
                </Accordion.Body>
            </Accordion.Item>
        </>
        )
    }

    const toggleSortOrder = () => 
    {
        let newConfig = {...config};
        if(newConfig.sortSavingsAscendingOrDescending === "ascending")
        {
            newConfig.sortSavingsAscendingOrDescending = "descending";
        }
        else
        {
            newConfig.sortSavingsAscendingOrDescending = "ascending";
        }
        setConfig(newConfig)
    };

    return(
      <Card>
        <Card.Header>
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <Card.Title>
                    Saving/Investment
                </Card.Title>
                <Button variant="secondary" onClick={toggleSortOrder}>
                    {config.sortSavingsAscendingOrDescending === "ascending" ? "Ascending" : "Descending"}
                </Button>
            </div>
        </Card.Header>
        <Card.Footer>
            <Button variant="primary" onClick={() => addSavingsAtDate()}>Add Date</Button>
        </Card.Footer>
        <Card.Body>
            <Accordion>
                {savingsAtDates.map((saving, index: number) => SavingsAtDate(saving, index))}
            </Accordion>
        </Card.Body>
      </Card>
    )
  };
  
  export default InputSavingsData;
  