import React from "react";
import { createContext, useState } from "react";

export interface MoneyStream
{
    name: string;
    cost: number;
};

export interface Debt
{
  name: string;
  monthlyRepayment: number
  minimumMonthlyRepayment: number
  total: number
  interest: number;
}

export interface Saving
{
  name: string;
  thisMonthsContribution: number;
  currentValue: number;
  accessibility: string;
  taxableIncome: boolean;
  riskScore: number;
}

export interface SavingsAtDate
{
    date: Date;
    investments: Saving[];
}

export interface SavingsGoal
{
    name: string;
    category: string;
    targetValue: number;
}

export interface Config
{
    sortSavingsAscendingOrDescending: "ascending" | "descending"
}

interface FinanceContextType
{
    savingGoals: SavingsGoal[];
    setSavingGoals: (goals: SavingsGoal[]) => void;
    updateSavingGoal: (index: number, updatedGoal: SavingsGoal) => void;
    addSavingGoal: () => void;
    deleteSavingGoal: (index: number) => void;

    config: Config
    setConfig: (config: Config) => void;

    incomes: MoneyStream[];
    updateIncome: (index: number, updatedIncome: MoneyStream) => void;
    addIncome: () => void;
    deleteIncome: (index: number) => void;
    setIncomes: (expenses: MoneyStream[]) => void;

    expenses: MoneyStream[];
    updateExpense: (index: number, updatedExpense: MoneyStream) => void;
    addExpense: () => void;
    deleteExpense: (index: number) => void;
    setExpenses: (expenses: MoneyStream[]) => void;
  
    debts: Debt[];
    updateDebt: (index: number, updatedDebt: Debt) => void;
    addDebt: () => void;
    deleteDebt: (index: number) => void;
    setDebts: (expenses: Debt[]) => void;
    
    getTotalExpense: () => number;
    getTotalIncome: () => number;
    getTotalDebt: () => number;
    
    savingsPerMonth: number;
    setSavingsPerMonth: (savingsPerMonth: number) => void;
    
    savingsAtDates: SavingsAtDate[];
    setSavingsAtDates: (savingsAtDate: SavingsAtDate[]) => void;
    updateSavingsAtDate: (index: number, updatedSavingsAtDate: SavingsAtDate) => void;
    addSavingsAtDate: () => void;
    deleteSavingsAtDate: (index: number) => void;
    duplicateSavingsAtDate: (index: number) => void;

    updateSavings: (dateIndex: number, index: number, updatedSavingsAtDate: Saving) => void;
    addSaving: (dateIndex: number) => void;
    deleteSaving: (dateIndex: number, index: number) => void;
}

const FinanceContext = createContext<FinanceContextType | undefined>(undefined);

export const FinanceProvider: React.FC<React.PropsWithChildren<{}>> = ({ children }) => {

    const [config, setConfig] = useState<Config>({sortSavingsAscendingOrDescending: "descending"})
    
    const [savingsPerMonth, setSavingsPerMonth] = useState(0);

    const [incomes, setIncomes] = useState<MoneyStream[]>([

    ]);

    const updateIncome = (index: number, updatedIncome: MoneyStream) => {
        setIncomes(prevIncomes =>
            prevIncomes.map((income, i) =>
                i === index ? updatedIncome : income
            )
        );
    };

    const addIncome = () => {
        const newIncome: MoneyStream = { name: '', cost: 0 };
        setIncomes(prevIncomes => [...prevIncomes, newIncome]);
    };

    const deleteIncome = (index: number) => {
        setIncomes(prevIncomes =>
            prevIncomes.filter((_, i) => i !== index)
        );
    };
    
    const [savingGoals, setSavingGoals] = useState<SavingsGoal[]>([

    ]);

    const updateSavingGoal = (index: number, updatedSavingGoal: SavingsGoal) => {
        setSavingGoals(prevSavingsGoal =>
            prevSavingsGoal.map((goal, i) =>
                i === index ? updatedSavingGoal : goal
            )
        );
    };

    const addSavingGoal = () => {
        const newGoal: SavingsGoal = { name: '', targetValue: 0, category: "" };
        setSavingGoals(prevGoals => [...prevGoals, newGoal]);
    };

    const deleteSavingGoal = (index: number) => {
        setSavingGoals(prevSavingsGoal =>
            prevSavingsGoal.filter((_, i) => i !== index)
        );
    };
    

    const [expenses, setExpenses] = useState<MoneyStream[]>([

    ]);
    
    const updateExpense = (index: number, updatedExpense: MoneyStream) => {
        setExpenses(prevExpenses =>
            prevExpenses.map((expense, i) =>
                i === index ? updatedExpense : expense
            )
        );
    };

    const addExpense = () => {
        const newExpense: MoneyStream = { name: '', cost: 0 };
        setExpenses(prevExpenses => [...prevExpenses, newExpense]);
    };

    const deleteExpense = (index: number) => {
        setExpenses(prevExpenses =>
            prevExpenses.filter((_, i) => i !== index)
        );
    };

    const [debts, setDebts] = useState<Debt[]>([

    ]);
    
    
    const addDebt = () => {
        const newDebt: Debt = { name: '', total: 0, interest: 0, monthlyRepayment: 0, minimumMonthlyRepayment: 0 };
        setDebts(prevDebts => [...prevDebts, newDebt]);
    };
    
    const updateDebt = (index: number, updatedDebt: Debt) => {
        setDebts(prevDebts =>
            prevDebts.map((debt, i) =>
                i === index ? updatedDebt : debt
            )
        );
    };

    const deleteDebt = (index: number) => {
        setDebts(prevDebts =>
            prevDebts.filter((_, i) => i !== index)
        );
    };

    const getTotalExpense = () => 
    {    
        return expenses.reduce((total, expense) => total + Number(expense.cost), 0);
    }

    const getTotalIncome = () => 
    {    
        return incomes.reduce((total, income) => total + Number(income.cost), 0);
    }  

    const getTotalDebt = () => 
    {    
        return debts.reduce((total, debt) => total + Number(debt.monthlyRepayment), 0);
    }

    const [savingsAtDates, setSavingsAtDates] = useState<SavingsAtDate[]>([
    ]);

    const updateSavingsAtDate = (index: number, updatedSavingsAtDate: SavingsAtDate) => {

        setSavingsAtDates(prevSavings =>
            prevSavings.map((saving, i) =>
                i === index ? updatedSavingsAtDate : saving
            )
        )
    };

    const addSavingsAtDate = () => {
        const newSavingsAtDate: SavingsAtDate = { date: new Date(), investments: [] };
        setSavingsAtDates(prevSavingsAtDate => [...prevSavingsAtDate, newSavingsAtDate]);
    };

    const deleteSavingsAtDate = (index: number) => 
    {
        setSavingsAtDates(prevSavingsAtDate => prevSavingsAtDate.filter((_, i) => i !== index));
    }

    const duplicateSavingsAtDate = (index: number) => {

        setSavingsAtDates(prevSavingsAtDate => {
            const itemToDuplicate = prevSavingsAtDate[index];
            const duplicatedItem = {
                ...itemToDuplicate,
                investments: [...itemToDuplicate.investments]
            };
            return [
                ...prevSavingsAtDate.slice(0, index + 1),
                duplicatedItem,
                ...prevSavingsAtDate.slice(index + 1)
            ];
        });
    };
    
    
    const updateSavings = (dateIndex: number, index: number, updatedSaving: Saving) => {
        setSavingsAtDates(prevSavingsAtDate =>
            prevSavingsAtDate.map((savingsAtDate, parentIndex) => 
            {
                if (parentIndex === dateIndex)
                {
                    return {
                        ...savingsAtDate, investments: savingsAtDate.investments.map((saving, i) => i === index ? updatedSaving : saving) 
                    }
                }
                else
                {
                    return savingsAtDate;
                }
            }
            )
        )
    };

    const addSaving = (dateIndex: number) => 
    {
        setSavingsAtDates(prevSavingsAtDate =>
            prevSavingsAtDate.map((savingsAtDate, parentIndex) => 
            {
                if (parentIndex === dateIndex)
                {
                    const newSaving: Saving = {name: "", thisMonthsContribution: 0, currentValue:0, accessibility: "sameDay", taxableIncome: false, riskScore: 1}
                    return {
                        ...savingsAtDate, investments: [...savingsAtDate.investments, newSaving]
                    }
                }
                else
                {
                    return savingsAtDate;
                }
            })
        )
    };

    const deleteSaving = (dateIndex: number, index: number) => 
    {
        setSavingsAtDates(prevSavingsAtDate =>
            prevSavingsAtDate.map((savingsForDate, parentIndex) => 
            {
                if (parentIndex === dateIndex)
                {
                    return {
                        ...savingsForDate, 
                        investments: savingsForDate.investments.filter((_, i) => i !== index)
                    };
                }
                else
                {
                    return savingsForDate;
                }
            })
        );
    };
  
  return (
    <FinanceContext.Provider value={{   config, setConfig,
                                        expenses, updateExpense, addExpense, deleteExpense, setExpenses, 
                                        incomes, updateIncome, addIncome, deleteIncome, setIncomes,
                                        savingGoals, updateSavingGoal, addSavingGoal, deleteSavingGoal, setSavingGoals,
                                        debts, updateDebt, addDebt, deleteDebt, setDebts,
                                        savingsAtDates, updateSavingsAtDate, addSavingsAtDate, deleteSavingsAtDate, duplicateSavingsAtDate, setSavingsAtDates,
                                        updateSavings, addSaving, deleteSaving,
                                        getTotalExpense, getTotalIncome, getTotalDebt,
                                        savingsPerMonth, setSavingsPerMonth,
                                     }}>
      {children}
    </FinanceContext.Provider>
  );
};

export const useFinance = () => {
  const context = React.useContext(FinanceContext);
  if (!context) {
      throw new Error('useFinance must be used within a FinanceProvider');
  }
  return context;
};