import { ChangeEvent } from "react";
import { useFinance } from "./FinanceContext"
import { Button } from "react-bootstrap";
import React from "react";

export const UploadFinanceData: React.FC = () =>
{
  const { setIncomes, setExpenses, setDebts, setSavingsPerMonth, setSavingsAtDates, setConfig,} = useFinance();

  const uploadJson = async (event: ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      const text = await file.text();
      try {
        const data = JSON.parse(text);
        if (data.incomes && Array.isArray(data.incomes)) {
          setIncomes(data.incomes);
        }
        if (data.expenses && Array.isArray(data.expenses)) {
          setExpenses(data.expenses);
        }
        if (data.debts && Array.isArray(data.debts)) {
          setDebts(data.debts);
        }
        if (data.savingsAtDates && Array.isArray(data.savingsAtDates)) {
          setSavingsAtDates(data.savingsAtDates);
        }
        if(data.savingsPerMonth)
        {
          setSavingsPerMonth(data.savingsPerMonth)
        }
        else
        {
          setSavingsPerMonth(0)
        }
        if(data.config)
        {
          setConfig(data.config)
        }
      } catch (error) {
        console.error('Error parsing JSON:', error);
      }
    }
  };

  const fileInputRef: React.RefObject<HTMLInputElement> = React.createRef();

  const handleButtonClick = () => {
    if(fileInputRef.current)
    {
      fileInputRef.current.click();
    }
  };
  
  return   (
    <>
    <input type="file" 
           accept="application/json" 
           onChange={uploadJson} 
           style={{display: "none"}}
           ref={fileInputRef}
           />
    
    <Button onClick={handleButtonClick}>Upload</Button>
    </>
  )
}

export const DownloadFinanceData: React.FC = () =>
{
    const {incomes, expenses, debts, savingsPerMonth, savingsAtDates, config, } = useFinance();

    const downloadJson = () => 
    {
        const data = {incomes, expenses, debts, savingsPerMonth, savingsAtDates, config}
        const json = JSON.stringify(data, null, 2); // Pretty print JSON with 2-space indentation
        const blob = new Blob([json], { type: 'application/json' });
        const url = URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = url;
        a.download = 'financeData.json';
        a.click();
        URL.revokeObjectURL(url);
    }

    return(
        <Button onClick={downloadJson}>Download</Button>
    )
};

export default DownloadFinanceData;