import { Button, Card, Col, Form, InputGroup, Row, Table } from "react-bootstrap";
import MoneyStreams, { MoneyStreamsProps } from "./MoneyStreams";
import { ChangeEvent, Component } from "react";
import { Debt, MoneyStream } from "./FinanceContext";

export interface DebtStreamsProps
{
    name: string;

    debts: Debt[];
    updateDebt: (index: number, updatedDebt: Debt) => void;
    addDebt: () => void;
    deleteDebt: (index: number) => void;
};

class DebtStreams extends Component<DebtStreamsProps>
{
    constructor(props: DebtStreamsProps)
    {
        super(props)
    }

    renderDebt = (debt: Debt, index: number) => {
        const { updateDebt, deleteDebt } = this.props;

        return (
            <>
                <tr>
                    <td>
                        <Form.Control
                            type='text'
                            onChange={(e: { target: { value: string; }; }) => updateDebt(index, { ...debt, name: e.target.value })}
                            value={debt.name}
                            className="mr-2" />
                    </td>
                    <td>
                        <Form.Control
                            type='number'
                            onChange={(e: ChangeEvent<HTMLInputElement>) => 
                                updateDebt(index, { ...debt, minimumMonthlyRepayment: parseFloat(e.target.value) })
                            }
                            value={debt.minimumMonthlyRepayment}
                            className="mr-2" />
                    </td>
                    <td>
                        <Form.Control
                            type='number'
                            onChange={(e: ChangeEvent<HTMLInputElement>) => 
                                updateDebt(index, { ...debt, monthlyRepayment: parseFloat(e.target.value) })
                            }
                            value={debt.monthlyRepayment}
                            className="mr-2" />
                    </td>
                    <td>
                        <Form.Control
                            type='number'
                            onChange={(e: ChangeEvent<HTMLInputElement>) => 
                                updateDebt(index, { ...debt, total: parseFloat(e.target.value) })
                            }
                            value={debt.total}
                            className="mr-2" />
                    </td>
                    <td>
                        <Form.Control
                            type='number'
                            onChange={(e: ChangeEvent<HTMLInputElement>) => 
                                updateDebt(index, { ...debt, interest: parseFloat(e.target.value) })
                            }
                            value={debt.interest}
                            className="mr-2" />
                    </td>
                    <td>
                        <Button variant="danger" onClick={() => deleteDebt(index)}>X</Button>
                    </td>
                </tr>
            </>
        );
    };

    render(){
        const { name, debts, addDebt } = this.props;

        return (
            <Card>
                <Card.Header>
                    <Card.Title>
                        {name}s
                    </Card.Title>
                </Card.Header>
                <Card.Body>
                    <Table striped bordered hover>
                        <thead>
                            <tr>
                                <th>Item</th>
                                <th>Minimum Monthly Repayment </th>
                                <th>Monthly Repayment </th>
                                <th>Total to Repay </th>
                                <th>Annual Interest %</th>
                            </tr>
                        </thead>
                        <tbody>
                            {debts.map((debt, index: number) => this.renderDebt(debt, index))}
                        </tbody>
                    </Table>
                    <Button variant="primary" onClick={addDebt}> Add {name} </Button>
                </Card.Body>
            </Card>
        );
    }
}

export default DebtStreams