import { ChangeEvent, Component } from "react";
import { Button, Card, Form, Table } from "react-bootstrap";
import { MoneyStream } from "./FinanceContext";

export interface MoneyStreamsProps
{
    name: string;

    streams: MoneyStream[];
    updateStream: (index: number, updatedExpense: MoneyStream) => void;
    addStream: () => void;
    deleteStream: (index: number) => void;
};

class MoneyStreams extends Component<MoneyStreamsProps>
{
    constructor(props: MoneyStreamsProps)
    {
        super(props)
    }
    
    renderStream = (expense: { name: string; cost: number; }, index: number) => {
        const { updateStream, deleteStream } = this.props;

        return (
            <>
                <tr>
                    <td>
                        <Form.Control
                            type='text'
                            onChange={(e: { target: { value: string; }; }) => updateStream(index, { ...expense, name: e.target.value })}
                            value={expense.name}
                            className="mr-2" />
                    </td>
                    <td>
                        <Form.Control
                            type='number'
                            onChange={(e: ChangeEvent<HTMLInputElement>) => 
                                updateStream(index, { ...expense, cost: parseFloat(e.target.value) })
                            }
                            value={expense.cost}
                            className="mr-2" />
                    </td>
                    <td>
                        <Form.Control
                            type='number'
                            onChange={(e: ChangeEvent<HTMLInputElement>) => 
                                updateStream(index, { ...expense, cost: parseFloat(e.target.value) })
                            }
                            value={(expense.cost * 12).toFixed(2)}
                            className="mr-2" 
                            readOnly/>
                    </td>
                    <td>
                        <Button variant="danger" onClick={() => deleteStream(index)}>X</Button>
                    </td>
                </tr>
            </>
        );
    };

    render(){
        const { name, streams, addStream } = this.props;

        return (
            <Card>
                <Card.Header>
                    <Card.Title>
                        {name}s
                    </Card.Title>
                </Card.Header>
                <Card.Body>
                    <Table striped bordered hover>
                        <thead>
                            <tr>
                                <th>Item</th>
                                <th>Monthly </th>
                                <th>Annual </th>
                                <th/>
                            </tr>
                        </thead>
                        <tbody>
                            {streams.map((stream, index: number) => this.renderStream(stream, index))}
                        </tbody>
                    </Table>
                    <Button variant="primary" onClick={addStream}>
                        Add {name}
                    </Button>
                </Card.Body>
            </Card>
        );
    }
};



export default MoneyStreams