import { Button, Card, Form, Table } from "react-bootstrap"
import { SavingsAtDate, SavingsGoal, useFinance } from "./FinanceContext"
import { ChangeEvent } from "react";
import { IndividualSavingCalculator, SavingsAtDateCalculator } from "./SavingsCalculators";

const SavingsGoals = () => {

    const {savingsAtDates, savingGoals, addSavingGoal, updateSavingGoal} = useFinance();



    const renderIndividualGoal = (goal: SavingsGoal, index: number) =>
    {
        
        const calculateTotalValue = (goal: SavingsGoal) =>
        {
            const getOrderedCopyOfSavingsAtDates = () =>
            {
                let copiedSavingsAtDates: SavingsAtDate[] = JSON.parse(JSON.stringify(savingsAtDates));
                let sortedSavingsAtDates= copiedSavingsAtDates.sort((copiedSavingAtDate1, copiedSavingAtDate2) => 
                    new Date(copiedSavingAtDate2.date).getTime() - new Date(copiedSavingAtDate1.date).getTime());
        
                return sortedSavingsAtDates;
            }

            const orderedDates = getOrderedCopyOfSavingsAtDates();
    
            if(orderedDates.length === 0)
            {
                return 0;
            }
    
            const mostRecentDate = orderedDates[0];
            const savingsCalculator = new SavingsAtDateCalculator(mostRecentDate, savingsAtDates);
            
            return savingsCalculator.totalValue;
        }

        return(
            <tr>
                <td>
                    <Form.Control
                        type='text'
                        onChange={(e: { target: { value: string; }; }) => updateSavingGoal(index, { ...goal, name: e.target.value })}
                        value={goal.name}
                        className="mr-2" />
                </td>
                <td>
                    <Form.Control
                        type='number'
                        onChange={(e: ChangeEvent<HTMLInputElement>) => 
                            updateSavingGoal(index, { ...goal, targetValue: parseFloat(e.target.value) })
                        }
                        value={goal.targetValue}
                        className="mr-2" />
                </td>
                <td>
                    {goal.targetValue}
                </td>
                <td>
                    {calculateTotalValue(goal)}
                </td>
            </tr>
        )
    }

    const renderGoals = () =>
    {
        return(
            <Table>
                <thead>
                    <tr>
                        <th>
                            Name
                        </th>
                        <th>
                            Goal Value
                        </th>
                        <th>
                            Which Savings To Include?
                        </th>
                        <th>
                            Current Value
                        </th>
                        <th>
                            Difference
                        </th>
                    </tr>
                </thead>
                <tbody>
                    {savingGoals.map((goal, index) => renderIndividualGoal(goal, index))}
                </tbody>
            </Table>
        )
    }

    return (
        <Card>
            <Card.Header>
                <Card.Title>
                    Saving Goals
                </Card.Title>
            </Card.Header>
            <Card.Footer>
                <Button variant="primary" onClick={() => addSavingGoal()}>Add Goal</Button>
            </Card.Footer>
            <Card.Body>
                {renderGoals()}
            </Card.Body>
        </Card>
    )

}

export default SavingsGoals